import ModulesNavComponent from '../modules-nav/modules-nav.vue'
export default {
   name: 'WorkshopComponent',
   components: {
      ModulesNavComponent
   },
   data: () => ({
      i18n: {
         card1: {
            duration: {
               unit: '',
               title: ' '
            },
            last_update_date: ' ',
            start: ['', ''],
            level: {
               title: '',
               value: ['', '', '']
            }
         },
         card2: {
            authors: '',
            resutlBtn: ' ',
            resutl: ' ',
            description: '  ',
            used_technologies: ' '
         },
         card3: {
            here: '',
            title: '  ',
            text: '                 '
         }
      },
      loaded: true
   }),

   props: ['workshop'],
   created() {},
   watch: {
      $route(to, from) {}
   },
   methods: {
    toggleAvatar() {
      var isBreak = false
      document.querySelectorAll('.author').forEach((author) => {
        if (author.querySelector('.info').clientWidth >= (author.clientWidth - 58)) {
          document.querySelectorAll('.avatar').forEach((avatar) => {
            avatar.style.display = 'none'
          })
          isBreak = true
        } else {
          if (!isBreak) {
            document.querySelectorAll('.avatar').forEach((avatar) => {
              avatar.style.display = 'inline-flex'
            })
          }
        }
      })
    }
   },
   mounted() {
    window.addEventListener('resize', this.toggleAvatar)

    this.$nextTick(function() {
      this.toggleAvatar()
    })
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.onResize)
  }
}



